.loader {
  margin-bottom: 40px;
}

.loaderInner {
  height: 60px;
  width: 100px;
}

.loaderLineWrap {
  animation: spin 2000ms cubic-bezier(0.175, 0.885, 0.32, 1.275) infinite;
  box-sizing: border-box;
  height: 50px;
  overflow: hidden;
  position: absolute;
  transform-origin: 50% 100%;
  width: 100px;
}
.loaderLine {
  border: 4px solid transparent;
  border-radius: 100%;
  box-sizing: border-box;
  height: 100px;
  left: 0;
  margin: 0 auto;
  position: absolute;
  right: 0;
  top: 0;
  width: 100px;
}
.loaderLineWrap:nth-child(1) {
  animation-delay: -50ms;
}
.loaderLineWrap:nth-child(2) {
  animation-delay: -100ms;
}
.loaderLineWrap:nth-child(3) {
  animation-delay: -150ms;
}
.loaderLineWrap:nth-child(4) {
  animation-delay: -200ms;
}
.loaderLineWrap:nth-child(5) {
  animation-delay: -250ms;
}

.loaderLineWrap:nth-child(1) .loaderLine {
  border-color: hsl(0, 80%, 60%);
  height: 90px;
  width: 90px;
  top: 7px;
}
.loaderLineWrap:nth-child(2) .loaderLine {
  border-color: hsl(60, 80%, 60%);
  height: 76px;
  width: 76px;
  top: 14px;
}
.loaderLineWrap:nth-child(3) .loaderLine {
  border-color: hsl(120, 80%, 60%);
  height: 62px;
  width: 62px;
  top: 21px;
}
.loaderLineWrap:nth-child(4) .loaderLine {
  border-color: hsl(180, 80%, 60%);
  height: 48px;
  width: 48px;
  top: 28px;
}
.loaderLineWrap:nth-child(5) .loaderLine {
  border-color: hsl(240, 80%, 60%);
  height: 34px;
  width: 34px;
  top: 35px;
}

@keyframes spin {
  0%,
  15% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
